.nav-top-l4 {
  border-left: 1px solid var(--coffee-milk);
  padding-left: 32px;
  margin-left: 32px;
  // visibility: hidden;
  // opacity: 0;

  &__item {
    height: 33px;
    position: relative;

    &:hover {
      color: var(--main-bronze);
      transition: 0.3s;
    }

    &-more {
      margin-top: 12px;
    }
  }

  &__link {
    color: var(--main-black);
    text-transform: uppercase;
    font-size: 14px;
    line-height: 33px;
    vertical-align: middle;
    margin-right: 34px;
    transition: 0.3s;

    @include hover {
      color: var(--dark-bronze);
    }

    .active {
      color: var(--main-bronze);
    }

  }
}


.piclink {
  display: inline-block;
  max-width: 308px;
  margin-left: 32px;
  &__link {
    text-transform: uppercase;
    font-size: 18px;
    color: var(--main-black);
    &:hover {
        color: var(--main-bronze);
        transition: 0.3s;
      }

  }
  &__pic {
    max-width: 308px;
    margin-bottom: 16px;
  }
}
