}

.page {
  height: 100%;
  font-family: var(--font-family, Roboto, sans-serif);
  -webkit-text-size-adjust: 100%;
}

body {
  background-color: var(--main-beige);
  font-family: var(--font-family, Roboto, sans-serif);
  font-size: 18px;
}

.page__body {
  margin: 0;
  min-width: 350px;
  min-height: 100%;
  font-size: 16px;
}

.page__body.main {
  background-color: var(--color-black);
}

section {
  min-width: 320px;
}

.main {
  margin-top: var(--header-height);
  background-color: var(--color-black);
}

.text-normal {
  font-size: 22px;
  line-height: 27px;
}

.text-medium {
  font-size: 18px;
  line-height: 22px;
}

.text-small {
  font-size: 14px;
  line-height: 22px;
}

h1.heading {
