}
@media (max-width: 1279px) {
  h1.heading {
    margin-top: 0;
  }
}

@media (min-width: 1280px) {
  .mobile {
    /* and (max-width: (1440px)) */
    display: none !important;
  }
}

@media (max-width: 1279px) {
  .desktop {
    display: none !important;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

a, p, span, div, button {
