  text-decoration: underline;
  margin: 16px 5px;
}

.btn-filter {
  /* Style the accordion panel. Note: hidden by default */
}
.btn-filter__item {
  margin-bottom: 6px;
  text-align: left;
  color: var(--main-white);
}
.btn-filter__item > .accordion {
  cursor: pointer;
  height: 49px;
  width: 100%;
  max-width: 280px;
  display: block;
  padding: 12px 24px;
  text-align: left;
  color: var(--main-black);
  background-color: transparent;
  border: 1px solid var(--coffee-milk);
  border-radius: 50px;
  transition: 0.3s ease;
  transition: 0.4s;
}
@media (any-hover: hover) {
  .btn-filter__item > .accordion:hover {
    color: var(--main-white);
    background-color: var(--main-bronze);
    cursor: pointer;
  }
}
.btn-filter__item > .accordion.active {
  color: var(--main-white);
  background-color: var(--main-bronze);
}
.btn-filter__item > .accordion:hover {
  color: var(--main-white);
}
.btn-filter__item > .accordion:hover:after {
  color: var(--main-white);
}
.btn-filter__item .empty {
  text-transform: uppercase;
  transition: 0.3s;
}
.btn-filter__item > .panel {
  margin: 0 32px;
  color: var(--main-black);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}
.btn-filter__item > .accordion:after {
  content: "˅";
  font-size: 18px;
  color: var(--main-bronze);
  float: right;
  margin-left: 12px;
  transform: scalex(2.5);
}
.btn-filter__item > .active:after {
  padding-top: 2px;
  color: var(--main-white);
  content: "^";
}

.filter2nd__btn {
  border: 0;
  border-radius: 0;
  color: var(--main-bronze);
  text-transform: uppercase;
  display: block;
  width: 100%;
  padding: 16px 0 5px 0;
